import StageObject from '../components/stageobject.class.js';

export default class Box extends StageObject {
  constructor(obj) {
    super(obj);
    this.tlRadius = this.trRadius = this.brRadius = this.blRadius = 0;
  }

  setRadius(t, r, b, l) {
    if(Number.isInteger(t) && !r && !b && !l) {
      this.setRadius(t, t, t, t);
      return this;
    }

    if(Number.isInteger(t) && Number.isInteger(r) && !b && !l) {
      this.setRadius(t, r, t, r);
      return this;
    }

    this.set({
        'tlRadius': t, 'trRadius': r,
        'brRadius': b, 'blRadius': l,
    }, false);

    return this;
  }

  imagine(ctx) {
    let r = this.left + this.width;
    let b = this.top + this.height;
    ctx.beginPath();
    ctx.moveTo(this.left + this.tlRadius, this.top);

    ctx.lineTo(r-this.tlRadius, this.top);
    ctx.quadraticCurveTo(r, this.top, r, this.top+this.trRadius);

    ctx.lineTo(r, this.top + this.height-this.trRadius);
    ctx.quadraticCurveTo(r, b, r-this.brRadius, b);

    ctx.lineTo(this.left + this.brRadius, b);
    ctx.quadraticCurveTo(this.left, b, this.left, b - this.blRadius);

    ctx.lineTo(this.left, this.top + this.blRadius);
    ctx.quadraticCurveTo(this.left, this.top,
      this.left + this.tlRadius, this.top);
    ctx.closePath();
  }

  draw(ctx) {
    super.draw(ctx);
    this.imagine(ctx);

    ctx.stroke();
    ctx.fill();
  }
}



